import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-myblogpage',
  templateUrl: './myblogpage.component.html',
  styleUrls: ['./myblogpage.component.css']
})
export class MyblogpageComponent implements OnInit {

    student={
        _id: '',
        name: '',
        email: '',
        password:''
    };
    singleblog={
        _id:'',   
        title:'',
        writer:'',
        about:'',
        content:'',
        writerid:'',
        approve:''
    }

  constructor() { }

  ngOnInit(): void {
    this.student = JSON.parse(localStorage.getItem('stdata'));
    this.singleblog = JSON.parse(localStorage.getItem('singleblogdata'));
  }

}
