import { Component, OnInit } from '@angular/core';
import {Validators} from '@angular/forms';
import {FormBuilder} from '@angular/forms';
import { BlogService } from '../service/blog.service';
import{Router} from '@angular/router';

@Component({
  selector: 'app-upload',
  templateUrl: './upload.component.html',
  styleUrls: ['./upload.component.css']
})
export class UploadComponent implements OnInit {

    student={
        _id: '',
        name: '',
        email: '',
        password: ''
    };

    blog={
        title:'',
        writer:'',
        about:'',
        content:'',
        writerid: '',
        approve:''
    }

  constructor(private fb : FormBuilder,private blogdata: BlogService,private router: Router) { }

  ngOnInit(): void {
    this.student = JSON.parse(localStorage.getItem('stdata'));
  }
  blogForm= this.fb.group(
    {   
        title:['',Validators.required],
        about:['',Validators.required],
        content:['',Validators.required]
    }
);

  blogsav(stid:any,stname:any){
      this.blog.writerid= stid;
      this.blog.writer= stname;
      this.blog.approve= 'false'
    this.blogdata.saveblog(this.blog).subscribe(
        res=>{
            console.log(res)
        },
        err => {
            if(err.status == 200){
                this.router.navigate(['/studenthome'])
            }
          }
    )
  }

}
