import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class StudentService {

    private stUrl = "/api"
    // "http://localhost:3000"


  constructor(private http: HttpClient) { }

  studentreg(student:any)
  {
    return this.http.post<any>(this.stUrl+"/stregister", student);
  }
  studentlog(login:any)
  {
    return this.http.post<any>(this.stUrl+"/stlogin", login);
  }
  adminreg(student:any)
  {
    return this.http.post<any>(this.stUrl+"/adregister", student);
  }
  adminlog(login:any)
  {
    return this.http.post<any>(this.stUrl+"/adlogin", login);
  }
}
