import { Component, OnInit } from '@angular/core';
import { BlogService } from '../service/blog.service';
import{Router} from '@angular/router';

@Component({
  selector: 'app-sthome',
  templateUrl: './sthome.component.html',
  styleUrls: ['./sthome.component.css']
})
export class SthomeComponent implements OnInit {

    student={
        _id: '',
        name: '',
        email: '',
        password: ''
    };
    blog=[{
        _id:'',   
        title:'',
        writer:'',
        about:'',
        content:'',
        writerid:''
    }]

  constructor(private blogdata: BlogService,private router: Router) { }

  ngOnInit(): void {
    this.student = JSON.parse(localStorage.getItem('stdata'));
    this.blogdata.blogs().subscribe((data)=>{
        this.blog=JSON.parse(JSON.stringify(data));
    })
  }

  searchblog(blogid:any){
    this.blogdata.singleblog(blogid).subscribe((data)=>{
        localStorage.setItem('singleblogdata',JSON.stringify(data))
        this.router.navigate(['/blog/'+blogid]);
    })
    
  }

  myblog(writerid:any){
    localStorage.setItem('writerid',JSON.stringify(writerid))
    this.router.navigate(['/myblogs/'+writerid]);
    }

}
