import { Component, OnInit } from '@angular/core';
import {Validators} from '@angular/forms';
import {FormBuilder} from '@angular/forms';
import { StudentService } from '../service/student.service';
import{Router} from '@angular/router';

@Component({
  selector: 'app-stregister',
  templateUrl: './stregister.component.html',
  styleUrls: ['./stregister.component.css']
})
export class StregisterComponent implements OnInit {

    student={
        name:'', 
        email:'',
        password:''
    }

  constructor(private fb : FormBuilder,private register: StudentService,private router: Router) { }

  ngOnInit(): void {
  }
  stregisterForm= this.fb.group(
    {   
        name:['',Validators.required],
        email:['',[Validators.pattern('^[a-z0-9,%+]+@[a-z0-9.-]+\.[a-z]{2,4}$'),Validators.required]],
        password:['',[Validators.minLength(8),Validators.required]]
    }
);

stregister(){
    this.register.studentreg(this.student).subscribe(
        res=>{
            console.log(res)
        },
        err => {
            if(err.status == 401){
                document.getElementById('alert').innerHTML="<div class=alert&#32;alert-danger role=alert>Already SignUp with this Email Id.<br>Use another Email Id.</div>";
                this.stregisterForm.reset();
            }
            else{
                this.router.navigate(['/studentlogin'])
            }
          }
    )
}

}
