import { Component, OnInit } from '@angular/core';
import {Validators} from '@angular/forms';
import {FormBuilder} from '@angular/forms';
import { StudentService } from '../service/student.service';
import{Router} from '@angular/router';

@Component({
  selector: 'app-stlogin',
  templateUrl: './stlogin.component.html',
  styleUrls: ['./stlogin.component.css']
})
export class StloginComponent implements OnInit {
    
    student={ 
        email:'',
        password:''
    }

  constructor(private fb : FormBuilder,private login: StudentService,private router: Router) { }

  ngOnInit(): void {
    localStorage.clear();
  }

  stloginForm= this.fb.group(
    {            
      email:['',[Validators.pattern('^[a-z0-9,%+]+@[a-z0-9.-]+\.[a-z]{2,4}$'),Validators.required]],
      password:['',[Validators.minLength(8),Validators.required]]
    }
  );

  stlogin(){
      console.log(this.student)
      this.login.studentlog(this.student)
      .subscribe(
      res=>{
          localStorage.setItem('token',res.token)
          localStorage.setItem('stdata',JSON.stringify(res.stdata))
          this.router.navigate(['/studenthome'])
      },
      err => {
          console.log(err);
          document.getElementById('alert').innerHTML="<div class=alert&#32;alert-danger role=alert>Enter a Valid Email or Password!</div>";
          this.stloginForm.reset();
        }
  )
  }
}
