import { Component, OnInit } from '@angular/core';
import {Validators} from '@angular/forms';
import {FormBuilder} from '@angular/forms';
import { StudentService } from '../service/student.service';
import{Router} from '@angular/router';

@Component({
  selector: 'app-adlogin',
  templateUrl: './adlogin.component.html',
  styleUrls: ['./adlogin.component.css']
})
export class AdloginComponent implements OnInit {

    admin={ 
        email:'',
        password:''
    }

  constructor(private fb : FormBuilder,private login: StudentService,private router: Router) { }

  ngOnInit(): void {
    localStorage.clear();
  }

  adloginForm= this.fb.group(
    {            
      email:['',[Validators.pattern('^[a-z0-9,%+]+@[a-z0-9.-]+\.[a-z]{2,4}$'),Validators.required]],
      password:['',[Validators.minLength(8),Validators.required]]
    }
  );

  adlogin(){
      console.log(this.admin)
      this.login.adminlog(this.admin)
      .subscribe(
      res=>{
          localStorage.setItem('token',res.token)
          localStorage.setItem('addata',JSON.stringify(res.addata))
          this.router.navigate(['/adminhome'])
      },
      err => {
          console.log(err);
          document.getElementById('alert').innerHTML="<div class=alert&#32;alert-danger role=alert>Enter a Valid Email or Password!</div>";
          this.adloginForm.reset();
        }
  )
  }

}
