import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { AdhomeComponent } from './adhome/adhome.component';
import { AdloginComponent } from './adlogin/adlogin.component';
import { BlogpageComponent } from './blogpage/blogpage.component';
import { HomeComponent } from './home/home.component';
import { MyblogpageComponent } from './myblogpage/myblogpage.component';
import { MyblogsComponent } from './myblogs/myblogs.component';
import { SthomeComponent } from './sthome/sthome.component';
import { StloginComponent } from './stlogin/stlogin.component';
import { StregisterComponent } from './stregister/stregister.component';
import { UpdateComponent } from './update/update.component';
import { UploadComponent } from './upload/upload.component';

const routes: Routes = [
    {
        path:'',
        component: HomeComponent
    },
    {
        path:'studentlogin',
        component: StloginComponent
    },
    {
        path:'studentregister',
        component: StregisterComponent
    },
    {
        path: 'adminlogin',
        component: AdloginComponent
    },
    {
        path: 'studenthome',
        component: SthomeComponent
    },
    {
        path: 'upload',
        component: UploadComponent
    },
    {
        path: 'blog/:id',
        component: BlogpageComponent
    },
    {
        path: 'myblogs/:id',
        component: MyblogsComponent
    },
    {
        path: 'adminhome',
        component: AdhomeComponent
    },
    {
        path: 'update',
        component: UpdateComponent
    },
    {
        path: 'myblogcontent/:id',
        component: MyblogpageComponent
    }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
